export default class AudioPlayer {
    // 音乐播放器
    constructor(url, volume, totalTime) {
        this.url = url;
        this.audioDOM = document.createElement('audio');
        this.audioDOM.src = url;
        this.audioDOM.load(); // 加载歌曲

        this.status = 'pause';
        this.totalTime = totalTime;
        this.setVolume(volume);
    }
    setTotalTime(time) { // 设置总时间 s
        this.totalTime = time;
    }
    play() {
        this.audioDOM.play();
        this.status = 'play';
    }
    pause() {
        this.audioDOM.pause();
        this.status = 'pause';
    }
    goToTime(time) {
        this.audioDOM.currentTime = time;
    }
    setVolume(volume) { // 设置声音大小
        volume = volume.toFixed(2);
        this.audioDOM.volume = volume;
    }
    resetCurTime() {
        this.audioDOM.currentTime = 0;
    }
    changeSong(url, totalTime) {
        this.resetCurTime();
        this.audioDOM.src = url
        this.audioDOM.load(); // 加载歌曲
        this.totalTime = totalTime;
    }

}